/* CSS RESET */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body,
h1 {
  margin: 0;
  color: #404040;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  font-size: 1.12rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: 2rem;
  line-height: 1.25;
  margin-bottom: 0.3rem;
  font-weight: 400;
}

h2 {
  font-weight: 400;
  font-size: 2rem;
  margin-bottom: 2rem;
}

h3 {
  line-height: 1;
  font-weight: 500;
  font-size: 1rem;
  margin-bottom: 1rem;
}

input, button {
  margin: 0;
  display: inline-block;
  vertical-align: middle;
  border-radius: 4px;
  font-size: 16px;
  line-height: 24px;
  border: 1px solid grey;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 11px;
  padding-right: 11px;
  background: #fff;
  box-sizing: border-box;
  height: 48px;
  min-width: 300px;
}

input:focus {
  outline: none;
  border-color: #1e94e8;
}

.section {
  margin: 3em 0;
}
@media (min-width: 768px) {
  .section {
    margin: 4em 0;
  }
}

.section-hero {
  padding-top: 2em;
  margin-top: 0;
}

.hero-container {
  border-top: 0 !important;
}
@media (min-width: 768px) {
  .hero-container {
    padding-top: 8em !important;
  }
}

.hero {
  position: relative;
  text-align: center;
}
.hero-heading {
  margin-bottom: 0.75em;
  font-size: 2em;
}

.label {
  display: inline-block;
  vertical-align: middle;
  border-radius: 2px;
  text-transform: uppercase;
  background-color: #8867cd;
  color: #fff;
  font-size: 0.878em;
  font-weight: 700;
  padding: 0 12px;
  line-height: 24px;
  text-align: center;
  padding-top: 3px;
}
@media (min-width: 768px) {
  .label {
    font-size: 0.778em;
    padding-top: 1px;
  }
}

.label-container-outer {
  position: relative;
}
.label-container-inner {
  position: absolute;
  line-height: 24px;
  top: -13px;
  text-align: center;
  width: 100%;
  z-index: 10;
}

.section-bg,
.section-ruled {
  padding: 4em 0;
  margin-top: -4em;
}

.section-labeled {
  padding-top: 4em;
  margin-top: 5em;
  position: relative;
}
@media (min-width: 768px) {
  .section-labeled {
    padding-top: 5em;
  }
}

.section-ruled {
  border-top: 1px solid #e0e0d9;
  border-bottom: 1px solid #e0e0d9;
}

.section-ruled + .section-ruled {
  border-top: none;
}

.section-bg {
  background-color: #fafaf9;
}

.container {
  margin: 0 auto;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .container {
    max-width: 896px;
    padding: 0 30px;
  }
}
.container-nps {
  max-width: 896px;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container-nps {
    max-width: 956px !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.cols {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.cols:after {
  content: "";
  display: table;
  clear: both;
}
.cols > * {
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .cols > * {
    float: left;
    margin-right: -100%;
  }
}

@media (min-width: 768px) {
  .wrapper {
    width: 100%;
    margin-left: 0%;
  }
}

@media (min-width: 768px) {
  .heading-wrapper {
    width: 83.3%;
    margin-left: 8.3%;
  }
}

.row {
  display: flex;
  flex-direction: column;
  padding-bottom: 48px;
  margin-bottom: 47px;
  border-bottom: 1px solid #e0e0d9;
}

.row:last-child {
  padding-bottom: 0;
  margin-bottom: 0;
  border-bottom: none;
}
@media (min-width: 768px) {
  .row {
    flex-direction: row;
  }
}

.column {
  align-self: center;
  flex-shrink: 0;
  margin-bottom: 0;
}

.column:last-child {
  margin-bottom: 0;
}
@media (max-width: 768px) {
  .column {
    margin-bottom: 24px;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
}
.column-1 {
  flex-shrink: 1;
  -ms-flex-shrink: 1;
  -webkit-flex-shrink: 1;
  padding-right: 20px;
}

.row-1 .column-2,
.row-1 .column-3 {
  width: 148px;
  margin-right: 10px;
}
.row-1 .column-4 {
  width: auto;
  margin-right: 0;
}
@media (min-width: 768px) {
  .row-1 .column-4 {
    width: 438px;
  }
}
.row-2 .column-2,
.row-2 .column-3,
.row-2 .column-4 {
  width: 106px;
}
.row-2 .column-2 {
  margin-left: 21px;
  margin-right: 21px;
}
.row-2 .column-3 {
  margin-left: 31px;
  margin-right: 31px;
}

.row-2 .column-4 {
  margin-left: 166px;
  margin-right: 166px;
}

.row-3 .column-2,
.row-3 .column-4,
.row-3 .column-6 {
  width: 106px;
}

.row-3 .column-3,
.row-3 .column-5 {
  display: flex;
  justify-content: center;
  -ms-justify-content: center;
  -webkit-justify-content: center;
  width: 100%;
}

.row-3 .column-2 {
  margin-left: 178px;
}
@media (min-width: 768px) {
  .row-3 .column-3 {
    width: 45px;
  }
}
@media (min-width: 768px) {
  .row-3 .column-5 {
    width: 46px;
  }
}

.row-3 .column-6 {
  margin-right: 166px;
}

.group {
  border-radius: 4px;
  box-sizing: border-box;
  -webkit-transform: scale(1);
          transform: scale(1);
  width: 100%;
  position: relative;
  background-color: #fff;
  border-width: 1px;
  border-style: solid;
  padding: 48px 10px 0 10px;
  text-align: center;
  margin-top: 23px;
}

.group:after {
  content: "";
  display: table;
  clear: both;
}
@media (min-width: 768px) {
  .group {
    margin-top: 0;
  }
}

.emote {
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: -24px;
}

.emote-nps {
  border-radius: 24px;
  position: absolute;
  background-color: grey;
  margin: 0 auto;
  left: 0;
  right: 0;
  width: 48px;
  height: 48px;
  top: -24px;
}

.emote-label {
  text-transform: uppercase;
  color: #fff;
  font-weight: 700;
  font-size: 14px;
  line-height: 48px;
}

.totals {
  text-align: center;
}

.total {
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  -webkit-transform: scale(1);
          transform: scale(1);
  *display: inline;
  margin-right: 10px;
  margin-bottom: 24px;
}

.total:last-child {
  margin-right: 0;
}

.total-percent {
  font-size: 28px;
  line-height: 36px;
  margin-bottom: 3px;
}

.total-label {
  font-size: 14px;
  line-height: 18px;
}

.total .total-input {
  min-width: 0;
  width: 48px;
  padding: 0;
  text-align: center;
}

.group-promoters {
  color: green;
  border-color:green;
}

.total .total-input-promoters:focus {
  border-color:green;
}

.group-passives {
  color: grey;
  border-color: grey;
}

.group-detractors {
  color: red;
  border-color: red;
}

.group-nps {
  color: grey;
  border-color: grey;
}

.connect {
  position: absolute;
  display: none;
}
@media (min-width: 768px) {
  .connect {
    display: block;
  }
}
.inputs {
  left: 0;
  right: 0;
  margin: 0 auto;
}

.inputs-promoters,
.inputs-passives,
.inputs-detractors {
  bottom: -74px;
}

.totals-promoters {
  left: 52px;
  bottom: -74px;
}

.totals-detractors {
  right: 52px;
  bottom: -74px;
}

.operator {
  font-size: 42px;
  line-height: 12px;
}

.operator-equal {
  position: relative;
  top: 4px;
}

.instructions {
  font-size: 16px;
}
@media only screen and (max-width: 970px){
  .instructions{
    font-size: 12px;
  }
}

@media only screen and (max-width:850px){
  .instructions {
    display: none;
  }
}
@media only screen and (max-width: 600px){
  .instructions{
    font-size: 16px;
    display: block;
    text-align: center;
  }
}

.image-promoters {
  width: 48px;
  height: 48px;
  background-size: 48px 48px;
  background-image: url(data:image/png;base64,);
}

.image-passives {
  width: 48px;
  height: 48px;
  background-size: 48px 48px;
  background-image: url(data:image/png;base64,);
}

.image-detractors {
  width: 48px;
  height: 48px;
  background-size: 48px 48px;
  background-image: url(data:image/png;base64,);
}

.image-nps {
  width: 48px;
  height: 48px;
  background-size: 48px 48px;
  background-image: url(data:image/png;base64,);
}

.inputs-image-promoters {
  width: 106px;
  height: 74px;
  background-size: 106px 74px;
  background-image: url(data:image/png;base64,);
}

.inputs-image-passives {
  width: 106px;
  height: 74px;
  background-size: 106px 74px;
  background-image: url(data:image/png;base64,);
}

.inputs-image-detractors {
  width: 396px;
  height: 74px;
  background-size: 396px 74px;
  background-image: url(data:image/png;base64,);
}

.totals-image-promoters {
  width: 164px;
  height: 74px;
  background-size: 164px 74px;
  background-image: url(data:image/png;base64,);
}

.totals-image-detractors {
  width: 158px;
  height: 74px;
  background-size: 158px 74px;
  background-image: url(data:image/png;base64,);
}

.heading {
  text-align: left;
}
@media (min-width: 768px) {
  .heading {
    text-align: center;
  }
}

@media (max-width: 600px){
  .heading {
    text-align: center;
  }
}

.form {
  text-align: center;
  display: block;
}

.button {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: scale(1);
          transform: scale(1);
}

@media only screen and (max-width:600px){
  .button {
    display: flex;
    justify-content: center;
  }
}

#email,
#password {
  min-width: 120px;
  height: 45px;
  text-align: center;
}

#password{
  margin: 0.4rem;
}

#email {
  border-radius: 50px 10px 10px 50px;
}

#get-started, 
#google {
  height: 45px;
}
#get-started {
  border-radius: 10px 50px 50px 10px;
  color: #fff;
  min-width: 140px;
  background-color: #8867cd;
  font-size: 14px;
  font-weight: 550;
  margin-right: .4rem;
}

#google {
  background-image: url(data:image/png;base64,);
  background-position: 10px 9px;
  background-size: 25px 25px;
  background-repeat: no-repeat;
  margin-left: 0.4rem;
  border-radius: 50px;
  min-width: 120px;
  padding-left: 40px;
  font-size: 14px;
  font-weight: 600;
  color: #404040;
}

@media only screen and (max-width:820px){
  #email, 
  #password,
  #get-started,
  #google{
    border-radius: 50px;
  }
}
